/*
	HTML5 Reset :: style.css
	----------------------------------------------------------
	We have learned much from/been inspired by/taken code where offered from:

	Eric Meyer					:: https://meyerweb.com
	HTML5 Doctor				:: https://html5doctor.com
	and the HTML5 Boilerplate	:: https://html5boilerplate.com

-------------------------------------------------------------------------------*/

/* Let's default this puppy out
-------------------------------------------------------------------------------*/

html, body, body div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, menu, nav, section, time, mark, audio, video, details, summary {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font-weight: normal;
	vertical-align: baseline;
	background: transparent;
	line-height: 1;
}

article, aside, figure, footer, header, nav, section, details, summary {display: block;}

/* Handle box-sizing while better addressing child elements:
   https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
html {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

*,
*:before,
*:after {
	-webkit-box-sizing: inherit;
	        box-sizing: inherit;
}

/* consider resetting the default cursor: https://gist.github.com/murtaugh/5247154 */

/* Responsive images and other embedded objects
*/
img,
object,
embed {max-width: 100%; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none;}

/*
   Note: keeping IMG here will cause problems if you're using foreground images as sprites.
	 In fact, it *will* cause problems with Google Maps' controls at small size.
	If this is the case for you, try uncommenting the following:

#map img {
		max-width: none;
}
*/

/* force a vertical scrollbar to prevent a jumpy page */
html {overflow-y: scroll;}

/* we use a lot of ULs that aren't bulleted.
	don't forget to restore the bullets within content. */
ul {list-style: none;}

blockquote, q {quotes: none;}

blockquote:before,
blockquote:after,
q:before,
q:after {content: ''; content: none;}

a {margin: 0; padding: 0; font-size: 100%; vertical-align: baseline; background: transparent; color: inherit; text-decoration: none !important;}

del {text-decoration: line-through;}

abbr[title], dfn[title] {border-bottom: 1px dotted #000; cursor: help;}

/* tables still need cellspacing="0" in the markup */
table {border-collapse: collapse; border-spacing: 0;}
th {font-weight: bold; vertical-align: bottom;}
td {font-weight: normal; vertical-align: top;}

hr {display: block; height: 1px; border: 0; border-top: 1px solid #ccc; margin: 1em 0; padding: 0;}

input, select {vertical-align: middle;}

pre {
    white-space: pre; /* CSS2 */
    white-space: pre-wrap; /* CSS 2.1 */
    white-space: pre-line; /* CSS 3 (and 2.1 as well, actually) */
    word-wrap: break-word; /* IE */
}

input[type="radio"] {vertical-align: text-bottom;}
input[type="checkbox"] {vertical-align: bottom;}
.ie7 input[type="checkbox"] {vertical-align: baseline;}
.ie6 input {vertical-align: text-bottom;}

select, input, textarea {font: 99% sans-serif;}

table {font-size: inherit; font: 100%;}

small {font-size: 85%;}

strong {font-weight: bold;}

td, td img {vertical-align: top;}

/* Make sure sup and sub don't mess with your line-heights https://gist.github.com/413930 */
sub, sup {font-size: 75%; line-height: 0; position: relative;}
sup {top: -0.5em;}
sub {bottom: -0.25em;}

/* standardize any monospaced elements */
pre, code, kbd, samp {font-family: monospace, sans-serif;}

/* hand cursor on clickable elements */
.clickable,
label,
input[type=button],
input[type=submit],
input[type=file],
button {cursor: pointer;}

/* Webkit browsers add a 2px margin outside the chrome of form elements */
button, input, select, textarea {margin: 0;}

/* make buttons play nice in IE */
button,
input[type=button] {width: auto; overflow: visible;}

/* scale images in IE7 more attractively */
.ie7 img {-ms-interpolation-mode: bicubic;}

/* prevent BG image flicker upon hover
   (commented out as usage is rare, and the filter syntax messes with some pre-processors)
.ie6 html {filter: expression(document.execCommand("BackgroundImageCache", false, true));}
*/

/* let's clear some floats */
.clearfix:before, .clearfix:after { content: "\0020"; display: block; height: 0; overflow: hidden; }
.clearfix:after { clear: both; }
.clearfix { zoom: 1; }

@font-face {
	font-family: "Arame Bold";
	src: url('fonts/ArameBold.otf')
}

@font-face {
	font-family: "Arame Regular";
	src: url('fonts/ArameRegular.ttf')
}

@font-face {
	font-family: "Din Pro Bold";
	src: url('fonts/DinProBold.otf')
}

@font-face {
	font-family: "Din Pro Medium";
	src: url('fonts/DinProMedium.otf')
}

body {
	overflow-x: hidden;
	background-color: #12032B;
}

html {
	scroll-behavior: smooth;
}

img {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

p {
	font-family: 'Din Pro Medium';
	font-size: 1rem;
	line-height: 1.3;
}

h1 {
	font-family: 'Arame Bold';
	line-height: 1.3;
}
h2 {
	font-family: 'Arame Regular';
	line-height: 1.3;
}

a.button,
button {
	font-family: 'Din Pro Bold';
	font-size: 1.25rem;
	color: white;
	outline: none;
    background: #FF3923;
    border: none;
    border-radius: 60px;
	padding: 10px 60px;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

main {
	position: relative;
	z-index: 10;
}

code {
	font-family: Calibri, sans-serif;
	font-size: 1.125rem;
}

.card {
	position: relative;
	z-index: 5;
	border-radius: 11.1181px;
	background-color: white;
	-webkit-box-shadow: 0px 4.44723px 111.181px rgba(0, 0, 0, 0.35);
	        box-shadow: 0px 4.44723px 111.181px rgba(0, 0, 0, 0.35);
	padding: 30px;
	margin-bottom: 30px;
}

.card:hover {
	-webkit-box-shadow: 0px 4.44723px 111.181px rgba(69, 84, 253, 0.5);
	        box-shadow: 0px 4.44723px 111.181px rgba(69, 84, 253, 0.5);
	z-index: 6 !important;
}

.card h1 {
	font-size: 1.625rem;
	color: #5A55E1;
	position: absolute;
	top: 15px;
	-webkit-transition: font-size 0.3s;
	-o-transition: font-size 0.3s;
	transition: font-size 0.3s;
}

.card_no_hover {
	position: relative;
	z-index: 5;
	border-radius: 11.1181px;
	background-color: white;
	-webkit-box-shadow: 0px 4.44723px 111.181px rgba(0, 0, 0, 0.35);
	        box-shadow: 0px 4.44723px 111.181px rgba(0, 0, 0, 0.35);
	padding: 30px;
	margin-bottom: 30px;
}

.card_no_hover h1 {
	font-size: 1.625rem;
	color: #5A55E1;
	position: absolute;
	top: 15px;
	-webkit-transition: font-size 0.3s;
	-o-transition: font-size 0.3s;
	transition: font-size 0.3s;
}

.card:hover h1 {
	font-size: 2.375rem;
	color: #3531AA;
}

.card:hover a.button,
.card:hover button {
	display: block !important;
}

.action_description {
	background-color: white;
	border-radius: 60px;
	padding: 10px 25px;
}

.action_description h1 {
	font-size: 1.5rem;
	color: #3935B8;
	text-transform: uppercase;
	min-width: 568px;
    text-align: center;
}

ul.hoverList li:hover {
	color: #FD5842;
}

@media (max-width: 568px) {
	.action_description h1 {
		min-width: 0;
	}
}


